export const DialogEliminarExpedicion = ({ open, onClose, onSubmit }) => {
    return <Dialog open={open} onClose={onClose} scroll="body">
        <DialogTitle>Borrar Expedicin</DialogTitle>
        <DialogContent>
            <DialogContentText>
                Esta accin es irreversible Desea borrar la expedicin?
            </DialogContentText>
        </DialogContent>
        <DialogActions>
            <Button onClick={onClose}>Cancelar</Button>
            <Button onClick={onSubmit}>Borrar Expedicin</Button>
        </DialogActions>
    </Dialog>
}
